var app= require("../../common/common.js"); 
var id;
export default{
	data:function(){
		return {
			pageLoad:false, 
			pageHide:false,
			pageData:{},
		}
		
	},
	onLoad:function(option){
		id=option.id;
		this.getPage();
	},
	 
	methods:{
		getPage:function(){
			var that=this;
			uni.request({
				url:app.apiHost+"?m=article",
				data:{
					authcode: app.getAuthCode()
				},
				success:function(data){
					that.pageLoad=true;
					that.pageData=data.data.data;
					 
				}
			})
		} 
	},
}
